import React, { useState } from 'react';
import { Typography, Box, TextField, MenuItem, Button } from '@mui/material';
import { LocalizationProvider, DatePicker, TimePicker } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';

function Booking() {
    const [selectedDate, setSelectedDate] = useState(null);
    const [selectedTime, setSelectedTime] = useState(null);
    const [selectedEmployee, setSelectedEmployee] = useState('');

    const employees = [
        { id: 1, name: 'Anna' },
        { id: 2, name: 'Ewa' },
    ];

    const handleBooking = () => {
        if (selectedDate && selectedTime && selectedEmployee) {
            alert(`Zarezerwowano wizyt u ${selectedEmployee} dnia ${selectedDate.format('YYYY-MM-DD')} o godzinie ${selectedTime.format('HH:mm')}`);
        } else {
            alert('Prosz wypeni wszystkie pola.');
        }
    };

    return (
        <LocalizationProvider dateAdapter={AdapterDayjs}>
            <Box>
                <Typography variant="h4">Rezerwacja wizyty</Typography>
                <Box mt={2}>
                    <TextField
                        select
                        label="Wybierz pracownika"
                        value={selectedEmployee}
                        onChange={(e) => setSelectedEmployee(e.target.value)}
                        fullWidth
                        margin="normal"
                    >
                        {employees.map((employee) => (
                            <MenuItem key={employee.id} value={employee.name}>
                                {employee.name}
                            </MenuItem>
                        ))}
                    </TextField>
                    <DatePicker
                        label="Wybierz dat"
                        value={selectedDate}
                        onChange={(newValue) => setSelectedDate(newValue)}
                        renderInput={(params) => <TextField {...params} fullWidth margin="normal" />}
                    />
                    <TimePicker
                        label="Wybierz godzin"
                        value={selectedTime}
                        onChange={(newValue) => setSelectedTime(newValue)}
                        renderInput={(params) => <TextField {...params} fullWidth margin="normal" />}
                    />
                    <Button
                        variant="contained"
                        color="primary"
                        onClick={handleBooking}
                        sx={{ mt: 2 }}
                    >
                        Zarezerwuj
                    </Button>
                </Box>
            </Box>
        </LocalizationProvider>
    );
}

export default Booking;